<template>
      <div class="content">
          <Carte v-for="(e,index) in data" :key="index" :value="e" :delay="index"/>
      </div>
</template>

<script>
import Carte from './Carte'
import { mapState } from 'vuex'
export default {
  name: 'LiveGrid',
  components:{
      Carte
  },
  computed: mapState([
        'data'
  ]),
}
</script>


<style scoped>
.content{
    position: relative;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(200px, 250px));
    justify-content: center;
    gap: 50px;
    margin-top: 20px;
}
</style>
