<template>
  <div class="posteriori">
    <p class="counter">Mise à jour dans :{{count}}</p>
    <ChartLine
      v-if="loaded"
      :key="datacollection"
      :datacollection="datacollection"
    />
  </div>
</template>

<script>
import ChartLine from "./ChartLine";
import { mapState } from 'vuex'
export default {
  name: "Posteriori",
  components: {
    ChartLine,
  },
  computed: mapState([
        'data','count'
  ]),
  data() {
    return {
      loaded: false,
      label: [],
      linedata: [],
      datacollection: null,
    };
  },
  watch :{
    data:function(){
      this.label.push(Math.round(new Date().getTime() / 1000) % 60);
      this.linedata.push(this.data[0]);
      this.datacollection = {
        labels: this.label,
        datasets: [
          {
            label: "Data One",
            fill: false,
            borderColor : "#f87979",
            backgroundColor: "#f87979",
            data: this.linedata,
          },
        ],
      };
      this.loaded = true;
    }
  },
  mounted() {
    this.label.push(Math.round(new Date().getTime() / 1000) % 60);
    this.linedata.push(this.data[0]);
    this.datacollection = {
      labels: this.label,
      datasets: [
        {
          label: "Data One",
          fill: false,
          borderColor : "#f87979",
          backgroundColor: "#f87979",
          data: this.linedata,
        },
      ],
    };
    
    this.loaded = true;
  },
};
</script>


<style scoped>
.posteriori {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.counter{
  position: absolute;
  top: 0;
  left: calc(50%-5vw);
  font-size: 1.2em;
}
</style>
