<template>
  <div class="live">
      <header>
          <div class="filter">
              <fieldset>
                <legend>Trier par :</legend>
                <div>
                    <label for="batiment">Batiment :</label>
                    <select name="batiment" id="batiment">
                        <option value="U1">U1</option>
                        <option value="U2">U2</option>
                        <option value="U3">U3</option>
                        <option value="U4">U4</option>
                    </select>
                </div>
                <div>
                    <label for="fluide">Fluide</label>
                    <select name="fluide" id="fluide">
                        <option value="Eau">Eau</option>
                        <option value="Electricite">Electricite</option>
                        <option value="Temperature">Temperature</option>
                        <option value="Air">Air Comprime</option>
                    </select>
                </div>
              </fieldset>
              <div class="view">
                  <span @click="changeView('LiveTable')"><i class="fas fa-th-list"></i></span>
                  <span @click="changeView('LiveGrid')"><i class="fas fa-th-large"></i></span>
              </div>
          </div>
      </header>
      <div class="viewoption">
        <keep-alive>
            <transition name="component-fade" mode="out-in">
                <component :is="viewoption" ></component>
            </transition>
        </keep-alive>
      </div>
  </div>
</template>

<script>
import LiveTable from './LiveTable'
import LiveGrid from './LiveGrid'
import { mapState } from 'vuex'
export default {
  name: 'Live',
  components:{
      LiveTable,
      LiveGrid
  },
  computed: mapState([
        'data'
  ]),
  data(){
      return{
          viewoption:'LiveTable'
      }
  },
  methods:{
      changeView(e){
          this.viewoption=e;
      }
  },
}
</script>


<style scoped>
.live{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: scroll;
}
header{
    width: 100%;
    height: 10vh;
    background: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
}
.filter,.view{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.view{
    width: 100px;
}
fieldset{
    width: 50%;
    padding: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
select{
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    margin-left: 10px;
}
span{
    cursor: pointer;
}
span:active .fas{
    color: blue;
}
.viewoption{
    width: 100%;
    min-height: 90vh;
}

.component-fade-enter-active,
.component-fade-leave-active {
  transition: opacity 0.3s ease;
}

.component-fade-enter-from,
.component-fade-leave-to {
  opacity: 0;
}
@media screen and (max-width: 800px){
    fieldset{
        width: 50%;
        padding: 0px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
}
</style>
