<template>
          <div class="carte">
              <div :class="{image :true , alert: value==0}">
                    <div :style=" {top:getPosition}" :class="{wave:true , alert:value>25 || value==0}"></div>
                    <div class="value">
                        <img src=".././assets/1.png" >
                    </div>
                    <span>{{value}}</span>
              </div>
              <div class="contenu">
                  <h2>Capteur 1</h2>
                  <div class="type">
                      <p>FLuide</p>
                      <p>U3</p>
                  </div>
              </div>
          </div>
</template>

<script>

export default {
  name: 'Carte',
  props:['value'],
  computed:{
      getPosition(){
          let val=180-((this.value*100)/30);
          let position=val+'%';
          return position;
      }
  },
}
</script>


<style scoped>
.carte{
    position: relative;
    justify-self: center;
    position: relative;
    width: 100%;
    height: 350px;
    border-radius: 10px;
    overflow: hidden;
    background:#fff;
    box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.1);
}
.carte::before{
    content: '';
    width: 50%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.2);
    z-index: 2;
}
.image{
    position: relative;
    width: 100%;
    height: 70%;
    transition: 1s;
    overflow: hidden;
}
.wave{
    position: absolute;
    top: calc(100% + 400px);
    left: 0;
    width: 100%;
    height: 100%;
    background: #7fff00;
    transition: 1s;
}
.alert.wave,.alert.image{
    background: #fd6456;
}
.alert.wave::before{
    background: url(../assets/wave-1.png);
}
.wave::before{
    content: '';
    position: absolute;
    top: -400px;
    left: 0;
    width: 100%;
    height: 400px;
    background: url(../assets/wave.png) repeat-x;
    animation: animate 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
    transform: translate3d(0, 0, 0);
    
}
@keyframes animate {
    0%{
        background-position-x: 0;
    }
    100%{
        background-position-x: 1440px;
    }
}
.contenu{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contenu h2{
    margin-top: 5px;
}
.contenu .type{
    position: relative;
    width: 100%;
    height: 50px;
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #469de5;
}
.contenu .type::before{
    position: absolute;
    top: 0;
    left: 50%;
    content: '';
    width: 1px;
    height: 100%;
    background: #000;
}
.contenu .type p{
    width: 50%;
    color: #fff;
    text-align: center;
}
.value{
    position: absolute;
    width: 100px;
    height: 100px;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.value img{
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
}
.carte span{
    position: absolute;
    bottom: -10%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 5em;
}
</style>
