<template>
  <div class="posteriori">
    <canvas id="myChart" width="400" height="400"></canvas>
  </div>
</template>

<script>
export default {
  name: "ChartLine",
  props: ["datacollection"],
  mounted() {
    var Chart = require("chart.js");
    var ctx = document.getElementById("myChart").getContext("2d");
    new Chart(ctx, {
      type: "line",
      data: this.datacollection,
      options: {
        legend: {
          display: false,
        },
      }
    });
  },
};
</script>


<style scoped>
#myChart {
  max-width: 600px;
  max-height: 600px;
}
</style>
