<template>
  <div class="app">
    <Nav @change-tab="changeTab"/>
    <div class="vue">
        <component :is="composantOngletActuel"></component>
    </div>
  </div>
</template>

<script>
import Nav from './components/Nav'
import Live from "./components/Live"
import Posteriori from "./components/Posteriori"
import Gestion from "./components/Gestion"
import store from './main'
export default {
  name: 'App',
  components: {
    Nav,
    Live,
    Posteriori,
    Gestion
  },
  store,
  data(){
    return {
      composantOngletActuel:'Live',
    }
  },
  methods:{
    changeTab(e){
      this.composantOngletActuel=e;
    },
    updateData(){
      if(this.$store.state.count==0){
        this.$store.commit('getData');
      }
    }
  },
  mounted:function(){
    this.$store.commit('getData');
    setInterval(() => {
          this.$store.commit('inc');
          this.updateData();
    }, 1000);
  }
}
</script>

