  left: -10px;
  width: 3px;
  height: 100%;
  background: #000;
}
li:last-child::before{
  content: '';
  position: absolute;
  top: 0;
  left: -10px;
  width: 3px;
  height: 50%;
  background: #000;
}
li:first-child::before{
  content: '';
